/*Home Page*/
.hero{
    width: 100%;
    height: 100vh;
    position: relative;
}

/*About Page*/
.hero-mid{
    width: 100%;
    height: 70vh;
    position: relative;
}
/*import is used for overriding from home page h1*/
.hero-mid h1{
    padding-top: 4rem !important;
}


/*Home page Img*/
img{
    width: 100%;
    height: 100%;
    background-size: cover;
    opacity: .8;
}

/*text inside home*/
.hero-text{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
}

.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background: #fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero-text p{
    font-size: 1.3rem;
    color: #fff;
    padding: 0.5rem 0 2rem 0;
}

/*Home page button*/
.hero-text .show{
    text-decoration: none;
    background: #fff;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: #222;
}
.hero-text .show:hover{
    background-color: #151516;
    color: #fff;
}

.hero-text .hide{
    display: none;
}

@media screen and (max-width: 555px){
    .hero-text h1{
        padding: 10px 20px;
    }

    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }

    .hero-text .show{
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
    }

    img{
        object-fit:cover;
    }
}